import React from 'react'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'
import { StaticQuery, graphql, Link } from 'gatsby'
import ninjaBanner from '../images/ninja-blue-banner.png'
import { Menu, Segment, Sidebar, Icon } from 'semantic-ui-react'

import Header from './header'
import Footer from './footer'
import 'semantic-ui-css/semantic.css'
import './layout.css'

class Layout extends React.Component {
  state = {
    visible: false,
  }
  render() {
    const { children, location } = this.props
    return (
      <StaticQuery
        query={graphql`
          query SiteTitleQuery {
            site {
              siteMetadata {
                title
              }
            }
          }
        `}
        render={data => (
          <>
            <Helmet
              title={data.site.siteMetadata.title}
              meta={[
                {
                  name: 'description',
                  content:
                    'LetFreeCode 從 2017 開始是一個實體的程式分享教學活動，希望透過實體面對面分享的方式，讓開發者們可以互相交流並且學習。',
                },
                {
                  name: 'keywords',
                  content:
                    'letfreecode, 免費實體教學, workshop, react, javascript',
                },
                {
                  name: 'image',
                  content: ninjaBanner,
                },
              ]}
            >
              <html lang="en" />
            </Helmet>
            <Sidebar.Pushable as={Segment}>
              <Sidebar
                as={Menu}
                animation="overlay"
                inverted
                icon="labeled"
                onHide={() =>
                  this.setState(state => ({
                    visible: !state.visible,
                  }))
                }
                vertical
                visible={this.state.visible}
              >
                <Menu.Item>
                  <Link
                    to="/"
                    style={{
                      color: 'color',
                      textDecoration: 'none',
                      fontSize: 20,
                    }}
                  >
                    <Icon name="home" />
                    <br />
                    LetFreeCode
                  </Link>
                </Menu.Item>
                <Menu.Item>
                  <Link
                    to="/workshops/"
                    style={{
                      color: 'color',
                      textDecoration: 'none',
                      fontSize: 20,
                    }}
                  >
                    <Icon name="world" />
                    <br />
                    工作坊列表
                  </Link>
                </Menu.Item>
                <Menu.Item>
                  <Link
                    to="/videos/"
                    style={{
                      color: 'color',
                      textDecoration: 'none',
                      fontSize: 20,
                    }}
                  >
                    <Icon name="book" />
                    <br />
                    線上資源
                  </Link>
                </Menu.Item>
                <Menu.Item>
                  <Link
                    to="/streams/"
                    style={{
                      color: 'color',
                      textDecoration: 'none',
                      fontSize: 20,
                    }}
                  >
                    <Icon name="video" />
                    <br />
                    直播資源
                  </Link>
                </Menu.Item>
              </Sidebar>
              <Sidebar.Pusher dimmed={this.state.visible}>
                <Header
                  onOpenMenu={() =>
                    this.setState(state => ({
                      visible: true,
                    }))
                  }
                  location={location}
                  siteTitle={data.site.siteMetadata.title}
                />
                <div>{children}</div>
                <Footer />
              </Sidebar.Pusher>
            </Sidebar.Pushable>
          </>
        )}
      />
    )
  }
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
